ade-up"}},[_vm._v("Project Daptar")]),_c('br'),_c('p',{attrs:{"data-aos":"fade-up","data-aos-delay":"100"}},[_vm._v(" If watching movies like "),_c('i',[_vm._v("Taare Zameen Par, Hichki, Super 30, Udaan, Iqbal, Paathshala")]),_vm._v(" give you goosebumps, we feel those goosebumps during every Daptar session. Daptar - the name resembles a school bag but the project focuses on teaching life skills that are quite often missing from a school bag. Under Daptar, we select ambassadors from the entire district who are interested and have the skill set required to teach students. The ambassadors are trained by professionals. From public speaking, effective communication, interpersonal skills to value for money, good touch bad touch, personal and environmental hygiene, the ambassadors are trained for every single thing under the sky that is necessary to survive in the real world. These ambassadors then visit the schools that have been taken under Rotaract District 3142 every week and impart these learnings to the students in form of activities and indirect learning methods. Daptar indeed is a bag full of emotions, you get to witness emotional hormones skyrocketing by the end of one academic year(Oh Gosh those tears'). ")])])])])]),_c('section',{staticClass:"about"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row justify-content-between"},[_c('div',{staticClass:"\n          col-lg-4\n          d-flex\n          align-items-center\n          justify-content-center\n          about-img\n        "},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../../assets/img/dohlogo.png"),"alt":"","data-aos":"zoom-in"}})]),_c('div',{staticClass:"col-lg-7 pt-5 pt-lg-0 content"},[_c('h1',{attrs:{"data-aos":"fade-up"}},[_vm._v("Drop of Hope")]),_c('br'),_c('p',{attrs:{"data-aos":"fade-up","data-aos-delay":"100"}},[_vm._v(" They say the start of anything has to be bang on the wall and we break the goddam wall by giving hope to people out there drop by drop. Drop of hope - a mega blood donation drive that happens across the entire district on the 1st of July. Why on 1st July? Why not anytime else? Well, we promote having a regular blood donation camp but July 1st is when the new Rotaract year starts and there’s no better deed than serving the community. Over the years, we’ve developed a brand of our own where donors along with the rotaractors eagerly wait for these camps to take place. This time we had Lokmat as our media partner and with the growing awareness campaigns across the globe, we anticipate collaborating with more brands in the years to come where we come together unanimously and promote the noble cause of donating blood. ")])])])])]),_c('section',{staticClass:"about"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row justify-content-between"},[_c('div',{staticClass:"\n          col-lg-4\n          d-flex\n          align-items-center\n          justify-content-center\n          about-img\n        "},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../../assets/img/next-the-conclave.png"),"alt":"","data-aos":"zoom-in"}})]),_c('div',{staticClass:"col-lg-7 pt-5 pt-lg-0 content"},[_c('h1',{attrs:{"data-aos":"fade-up"}},[_vm._v("Next: The Conclave")]),_c('br'),_c('p',{attrs:{"data-aos":"fade-up","data-aos-delay":"100"}},[_vm._v(" On a random Wednesday evening, tired of the monotonous life you double click yet another post on social media, surrounded by the guilt of not working toward the short term goals you had set for yourself which will eventually push the timeline of your long term goal you come across the post of this person and BANG!!! The person was the speaker of a session you had attended a year ago organized by Rotaract District 3142. That person had a similar story like yours but the only thing that they had an edge over you were they kept asking themselves \"WHAT’S NEXT?\" They asked it when they had a fall, they asked it when they reached a milestone, they asked it when things went south and north and center and all across. And BANG… you get that little dose of motivation required enough to keep you going. That’s NEXT - The Conclave for you, a motivational session where speakers from different walks of life come together at a single platform, share their stories with the attendees and inspire them to achieve all their dreams. The initiative had great success where various celebrity speakers like Prajakta Koli, Abhi-Niyu, Akshay Laxman, Sunil Jadhav, and many more have inspired the audiences with their stories. ")])])])])]),_c('section',{staticClass:"about"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row justify-content-between"},[_c('div',{staticClass:"\n          col-lg-4\n          d-flex\n          align-items-center\n          justify-content-center\n          about-img\n        "},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../../assets/img/Under18.jpeg"),"alt":"","data-aos":"zoom-in"}})]),_c('div',{staticClass:"col-lg-7 pt-5 pt-lg-0 content"},[_c('h1',{attrs:{"data-aos":"fade-up"}},[_vm._v("Under 18 Summit")]),_c('br'),_c('p',{attrs:{"data-aos":"fade-up","data-aos-delay":"100"}},[_vm._v(" Joan Collins famously quoted that Age is just a number. Under 18 Summit is a platform for young minds under the age of 18, to present their talent and inspire people. It provides teens with the opportunity to share their story ")])])])])]),_c('section',{staticClass:"about"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row justify-content-between"},[_c('div',{staticClass:"\n          col-lg-4\n          d-flex\n          align-items-center\n          justify-content-center\n          about-img\n        "},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../../assets/img/REDLOGO_final.png"),"alt":"","data-aos":"zoom-in"}})]),_c('div',{staticClass:"col-lg-7 pt-5 pt-lg-0 content"},[_c('h1',{attrs:{"data-aos":"fade-up"}},[_vm._v("Rotaract Entertainment Destination")]),_c('br'),_c('p',{attrs:{"data-aos":"fade-up","da